import { useState, useEffect, useRef } from "react";
import { OrbitControls } from "three/addons/controls/OrbitControls";
import * as THREE from "three";
import { GLTFLoader } from "three/addons/loaders/GLTFLoader";
import { RGBELoader } from 'three/addons/loaders/RGBELoader.js';
import { EffectComposer } from 'three/addons/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/addons/postprocessing/RenderPass.js';
import { UnrealBloomPass } from 'three/addons/postprocessing/UnrealBloomPass.js';
import theModel from "media/Chibi_Pet_07_04.glb";
import envMapImg from "media/hdr.hdr";

//https://github.com/stevinz/three-subdivide
import { LoopSubdivision } from 'utilities/LoopSubdivision.js';

function loadGLTFModel(scene, glbPath, subdivide, options) {
    const { receiveShadow, castShadow } = options;

    return new Promise((resolve, reject) => {
        const loader = new GLTFLoader();
        loader.load(
            glbPath,
            (gltf) => {
                let obj = gltf.scene;
                // console.log("obj:", obj);
                const params = {
                    split: false,
                    uvSmooth: true,
                    preserveEdges: true,
                    flatOnly: false,
                    maxTriangles: Infinity,
                };
                
                for (let i = 0; i < obj.children[0].children.length; i++) {
                    obj.children[0].children[i].geometry = LoopSubdivision.modify(obj.children[0].children[i].geometry, subdivide, params);
                    // obj.children[0].children[i].material.envMapIntensity = 2.0;
                    // obj.children[0].children[i].material.metalness = 1;
                    // obj.children[0].children[i].material.roughness = 0.3;
                    // obj.children[0].children[i].material.receiveShadow = true;
                    // obj.children[0].children[i].material.castShadow = true;
                    if (obj.children[0].children[i].material.name === 'Glow') {
                        obj.children[0].children[i].material.emissiveIntensity = 10000;
                        obj.children[0].children[i].material.emissive = {
                            b: 10,
                            g: 0.8,
                            isColor: true,
                            r: 0.5
                        };
                        // obj.children[0].children[i].material.color = {
                        //     b: 1,
                        //     g: 0.8,
                        //     isColor: true,
                        //     r: 0.5
                        // }
                    } else {
                        obj.children[0].children[i].material.envMapIntensity = 1.0;
                    }
                }

                obj.name = "chibi_pet_07";
                obj.receiveShadow = receiveShadow;
                obj.castShadow = castShadow;

                // const mesh = new THREE.Mesh(geometry, material);

                scene.add(obj);

                const directionalLight = new THREE.DirectionalLight(0xffffff, 4.0);
                directionalLight.position.x = 1;
                directionalLight.position.y = 2;
                directionalLight.position.z = 3;
                scene.add(directionalLight);

                const directionalLight2 = new THREE.DirectionalLight(0xffffff, 1.0);
                directionalLight2.position.x = -1;
                directionalLight2.position.y = 1;
                directionalLight2.position.z = -3;
                scene.add(directionalLight2);

                const directionalLight3 = new THREE.DirectionalLight(0xffffff, 0.5);
                directionalLight3.position.x = 0;
                directionalLight3.position.y = 0;
                directionalLight3.position.z = 3;
                scene.add(directionalLight3);

                const directionalLight4 = new THREE.DirectionalLight(0xffffff, 0.25);
                directionalLight4.position.x = -2;
                directionalLight4.position.y = -0.5;
                directionalLight4.position.z = 3;
                scene.add(directionalLight4);


                obj.traverse(function (child) {
                    if (child.isMesh) {
                        child.castShadow = castShadow;
                        child.receiveShadow = receiveShadow;
                    }
                });

                resolve(obj);
            },
            undefined,
            function (error) {
                console.log(error);
                reject(error);
            }
        );
    });
}

function easeOutCirc(x) {
    return Math.sqrt(1 - Math.pow(x - 1, 4));
}

const envHDR = async (scene) => {
    // Environment Lighting
    const hdrLoader = new RGBELoader();
    const envMap = await hdrLoader.loadAsync(envMapImg);
    envMap.mapping = THREE.EquirectangularReflectionMapping;
    scene.environment = envMap;
}

const bloom = true;

const DisplayModel = (modelURL) => {
    const refContainer = useRef();
    const [loading, setLoading] = useState(true);
    const [renderer, setRenderer] = useState();

    useEffect(() => {
        const { current: container } = refContainer;
        if (container && !renderer) {
            const scW = container.clientWidth;
            const scH = container.clientHeight;
            const renderer = new THREE.WebGLRenderer({
                antialias: true,
                alpha: true
            });
            renderer.setPixelRatio(window.devicePixelRatio);
            renderer.setSize(scW, scH);
            if (bloom === true) {
                // renderer.toneMapping = THREE.CineonToneMapping//THREE.ReinhardToneMapping; // ???
                renderer.toneMapping = THREE.ReinhardToneMapping;
                renderer.toneMappingExposure = 1.0;
            }
            renderer.outputEncoding = THREE.sRGBEncoding;
            container.appendChild(renderer.domElement);
            setRenderer(renderer);

            const scene = new THREE.Scene();

            envHDR(scene);


            const cameraDistance = 7.0;
            const camera = new THREE.PerspectiveCamera(40, scW / scH, 0.5, 100);

            const target = new THREE.Vector3(0, 0, 0);
            const initialCameraPosition = new THREE.Vector3(
                cameraDistance * Math.sin(0.2 * Math.PI),
                2,
                cameraDistance * Math.cos(0.2 * Math.PI)
            );

            const ambientLight = new THREE.AmbientLight(0xcccccc, 1);
            scene.add(ambientLight);
            const controls = new OrbitControls(camera, renderer.domElement);
            controls.autoRotate = true;
            controls.target = target;

            loadGLTFModel(scene, theModel, 2, {
                receiveShadow: false,
                castShadow: false
            }).then(() => {
                animate();
                setLoading(false);
            });

            let req = null;
            let frame = 0;
            const animate = () => {
                req = requestAnimationFrame(animate);
                frame = frame <= 100 ? frame + 1 : frame;

                if (frame <= 100) {
                    const p = initialCameraPosition;
                    const rotSpeed = -easeOutCirc(frame / 120) * Math.PI * 20;

                    camera.position.y = 2;
                    camera.position.x =
                        p.x * Math.cos(rotSpeed) + p.z * Math.sin(rotSpeed);
                    camera.position.z =
                        p.z * Math.cos(rotSpeed) - p.x * Math.sin(rotSpeed);
                    camera.lookAt(target);
                } else {
                    controls.update();
                }


                //Bloom Effects
                if (bloom === true) {
                    const params = {
                        exposure: 1,
                        bloomStrength: 0.25,
                        bloomThreshold: 0.5,
                        bloomRadius: 0
                    };

                    const renderScene = new RenderPass(scene, camera);

                    const bloomPass = new UnrealBloomPass(new THREE.Vector2(window.innerWidth, window.innerHeight), 1.5, 0.4, 0.85);
                    bloomPass.threshold = params.bloomThreshold;
                    bloomPass.strength = params.bloomStrength;
                    bloomPass.radius = params.bloomRadius;

                    const composer = new EffectComposer(renderer);
                    composer.addPass(renderScene);
                    composer.addPass(bloomPass);
                    composer.render();
                } else {
                    renderer.render(scene, camera);
                }


            };

            return () => {
                cancelAnimationFrame(req);
                renderer.dispose();
            };
        }
    }, [renderer]);

    return (
        <div
            style={{ height: "min(100vw,100vh)", width: "min(100vw,100vh)", position: "relative" }}
            ref={refContainer}
        >
            {loading && (
                <div style={{ width: '100%', height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                    <span style={{ textAlign: 'center' }}>
                        Loading...
                    </span>
                </div>
            )}
        </div>
    );
};

export default function RenderModel(modelURL) {
    return (
        <div style={{ width: "100%", margin: "0 auto" }}>
            <DisplayModel modelURL={modelURL} />
        </div>
    );
}
