import { SHA256 } from 'crypto-js';
import getJsonFromURL from 'utilities/urlVars/getJsonFromURL';
import RenderModel from 'components/renderModel';
import './App.css';

function App() {

  const openWebpage = () => {
    let obj = getJsonFromURL();
    if (obj && obj.a && typeof obj.a === 'string') {
      let value = SHA256(obj.a).toString();
      console.log("key:", value);
      if (value === "1c6c220dccc1fce47958becbb338132da5cfb8d99ccdd960c7c26bf1724515e3") {
        console.log("true");
        return true;
      } else {
        console.log("false");
        return false;
      }
    } else {
      console.log("false");
      return false;
    }
  };

  if (openWebpage()) {
    return (
      <div className="App">
        <div>
          <RenderModel />
        </div>
      </div>
    );
  } else {
    return (
      <div className="App">
        <p>
          Models
        </p>
      </div>
    );
  }
}

export default App;
